<template>
  <svg
      v-if="canHover"
      aria-hidden="true"
      style="cursor: pointer"
      @mouseover="isHover=true"
      @mouseout="isHover=false">
    <use :xlink:href="icon"></use>
  </svg>
  <svg
      v-else
      aria-hidden="true">
    <use :xlink:href="icon"></use>
  </svg>
</template>

<script>
export default {
  name: 'd2-icon-svg',
  props: {
    name: {
      type: String,
      required: true
    },
    canHover: {
      type: Boolean,
      default: false
    },
    hover: {
      type: Boolean,
      default: false
    }
  },
  data () {
    return {
      isHover: false
    }
  },
  computed: {
    icon () {
      return this.hover ? `#svg-${this.name}_slt` : this.isHover ? `#svg-${this.name}_slt` : `#svg-${this.name}`
    }
  }
}
</script>
